<template>
    <div class="popup">
        <div class="content">
            <button class="xClose" @click="this.$parent.isHistoryOpen = false"> X </button>
            <ul>
                <li v-for="element in list" :key="element"> {{element.disposalDate}} : {{element.performer}}</li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
    data () {
        return {
            historyRegEx: new RegExp('(0?[1-9]|[12][0-9]|3[01])\.(0?[1-9]|[1][0-2])\.[0-9]+'),
            list: JSON.parse(localStorage.getItem('disposalHistory'))
        }
    }
}
</script>

<style>
    .popup {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.7);
    }

    .xClose {
        position: fixed;
        top: 10px;
        right: 10px;
    }

    .content {
        max-width: 400px;
        width: 80vw;
        margin: 20vh auto;
        background: rgb(224, 182, 182);
        padding: 20px;
        border-radius: 10px;
    }

    ul {
        list-style-type: none;
        text-align: left;
    }
</style>