<template>
    <h1>Trash Schedule</h1>
    <p> <span>Next to Take Out the Garbage:</span> <span>{{organisedList[0]}}</span></p>
    <form @submit="handlingForm">
        Today: {{date}} trash was disposed by: 
        <select name="selectLastEmployee" id="selectLastEmployee" v-model="selectedEmp" required>
            <option v-for="employee in organisedList" :key="employee" :value="employee"> {{employee}} </option>
        </select>
        <button type="submit" :disabled="wasDisposed"> Confirm </button>
    </form>
</template>

<script>
export default {
    data () {
        return {
            defaultList: [
                'Jakub Stasiak',
                'Mariusz Siedlok',
                'Mariusz Tarkowski',
                'Michał Darmach',
                'Kamil Łopaciński'
            ],
            organisedList: [
                'Jakub Stasiak',
                'Mariusz Siedlok',
                'Mariusz Tarkowski',
                'Michał Darmach',
                'Kamil Łopaciński'
            ],
            date: new Date().toLocaleDateString("de-DE"),
            wasDisposed: false,
            selectedEmp: '',
            historyList: []
        }
    },
    methods: {
        checkForStorage: function () {
            if(localStorage.length === 0) {
                localStorage.setItem('employees', JSON.stringify(this.defaultList))
            } else {
                this.organisedList = JSON.parse(localStorage.getItem('employees'))
            }
        },

        handlingForm: function (e) {
            e.preventDefault()

            this.historyList.push({
                disposalDate: this.date,
                performer: this.selectedEmp
            })
            localStorage.setItem('disposalHistory', JSON.stringify(this.historyList))

            this.wasDisposed = true

            this.organisedList = this.organisedList.filter( element => element != this.selectedEmp)
            this.organisedList.push(this.selectedEmp)
            localStorage.setItem('employees', JSON.stringify(this.organisedList))
        }

    },
    beforeMount(){
        this.checkForStorage()

        if(!JSON.parse(localStorage.getItem('disposalHistory'))){
            return
        }
        this.historyList = JSON.parse(localStorage.getItem('disposalHistory'))
        
        JSON.parse(localStorage.getItem('disposalHistory')).forEach(element => {
            if(element.disposalDate == this.date) {
                this.wasDisposed = true
            }
        });
    }
}


</script>

<style scoped>
    button {
        margin-left: 10px;
    }
    p > span {
        display: inline-block;
    }
</style>